export default function (Component) {
  Component.__i18n = Component.__i18n || []
  Component.__i18n.push({
    "locale": "",
    "resource": {
      "ua": {
        "title": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Отримати допомогу"])},
        "name": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Ваше ім'я*"])},
        "phone": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Ваш телефон*"])},
        "email": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Ваш e-mail*"])},
        "message": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Опишіть Вашу проблему"])},
        "send": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Відправити"])},
        "cancel": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Скасувати"])}
      },
      "en": {
        "title": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Get help"])},
        "name": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Your name*"])},
        "phone": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Your phone*"])},
        "email": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Your e-mail*"])},
        "message": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Describe your problem"])},
        "send": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Send"])},
        "cancel": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Cancel"])}
      }
    }
  })
}
